import { heuristic } from './Util';
export class NNetwork {
    public graph: NGraph;

    public openNodes: NNode[] = [];
    public closedNodes: NNode[] = [];
    
    public startNode: NNode;
    public endNode: NNode;
    
    public path: NNode[] = [];
    
    public heuristicConst = 1;
    
    public doneComputation: boolean = false;
    public connectionDiagonal = false;
    constructor() {
        this.graph = new NGraph();
    }
    public buildConnection = (node: NNode, cols: number, rows: number) => {
        if (node.blocked) { return; }

        let x = node.x;
        let y = node.y;

        if (x < cols - 1) {
            if (!this.graph.grid[x + 1][y].blocked) {
                node.neighbors.push(this.graph.grid[x + 1][y]);
            } // right
        }

        if (y < rows - 1) {
            if (!this.graph.grid[x][y + 1].blocked) {
                node.neighbors.push(this.graph.grid[x][y + 1]);
            } // bottom
        }

        if (x > 0) {
            if (!this.graph.grid[x - 1][y].blocked) {
                node.neighbors.push(this.graph.grid[x - 1][y]);
            } // left
        }

        if (y > 0) {
            if (!this.graph.grid[x][y - 1].blocked) {
                node.neighbors.push(this.graph.grid[x][y - 1]);
            } // top
        }
        if (this.connectionDiagonal) {
            console.log(this.connectionDiagonal);
            if (x > 0 && y > 0) {
                if (!this.graph.grid[x - 1][y - 1].blocked) {
                    node.neighbors.push(this.graph.grid[x - 1][y - 1]);
                }
            } // left up
            if (x < cols - 1 && y < rows - 1) {
                if (!this.graph.grid[x + 1][y + 1].blocked) {
                    node.neighbors.push(this.graph.grid[x + 1][y + 1]);
                }
            } // right bottom
            if (x > 0 && y < rows - 1) {
                if (!this.graph.grid[x - 1][y + 1].blocked) {
                    node.neighbors.push(this.graph.grid[x - 1][y + 1]);
                }
            } // left bottom
            if (x < cols - 1 && y > 0) {
                if (!this.graph.grid[x + 1][y - 1].blocked) {
                    node.neighbors.push(this.graph.grid[x + 1][y - 1]);
                }
            } // right top
        }
    }
    public gePath() {
        let result = undefined;
        let count = this.graph.grid[0].length * this.graph.grid.length
        while (count--) {
            result = this.propagation();
            if (result !== undefined && result.length) {
                return result;
            }
        }
        return result;
    }
    public propagation() {
        // console.log('-----');
        if (this.openNodes.length > 0) {
            //find the lowest f in open nodes
            let lowestF = 0;
            for (let i = 0; i < this.openNodes.length; i++) {
                if (this.openNodes[i].f < this.openNodes[lowestF].f) {
                    lowestF = i;
                }

                if (this.openNodes[i].f === this.openNodes[lowestF].f) {
                    if (this.openNodes[i].g > this.openNodes[lowestF].g) {
                        lowestF = i;
                    }
                }
            }
            let current: NNode = this.openNodes[lowestF];

            if (current === this.endNode) {
                //reached the end node so wait a sec and restart
                this.doneComputation = true;
                return this.path;
            }

            //calculate path
            this.path = [];
            let temp = current;
            while (temp.previous) {
                this.path.push(temp.previous);
                temp = temp.previous;
            }
            // console.log(this.path.length);

            this.openNodes.splice(this.openNodes.indexOf(current), 1);
            this.closedNodes.push(current);

            let neighbors = current.neighbors;
            for (let i = 0; i < neighbors.length; i++) {
                let neighbor = neighbors[i];

                if (!this.closedNodes.includes(neighbor) && !neighbor.blocked) {
                    let tempG = current.g + heuristic(neighbor, current) * this.heuristicConst;

                    if (!this.openNodes.includes(neighbor)) {
                        this.openNodes.push(neighbor);
                    }
                    else {
                        continue;
                    }

                    neighbor.g = tempG;
                    neighbor.h = heuristic(neighbor, this.endNode);
                    neighbor.f = neighbor.g + neighbor.h;
                    neighbor.previous = current
                }
            }
            return undefined;
        } else {
            this.doneComputation = true;
            return this.path;
        }
    }
}
export class NGraph {
    public grid: NNode[][] = [];
    constructor() {

    }
}
export class NNode {
    public f = 0;
    public h = 0;
    public g = 0;

    public x = 0;
    public y = 0;
    public z = 0;
    public neighbors: NNode[] = [];
    public previous = undefined;
    public blocked = false;
    constructor(x: number, y: number) {
        this.f = 0;
        this.h = 0;
        this.g = 0;
        this.x = x;
        this.y = y;
        this.neighbors = [];
        this.blocked = false;
    }
}
