import { NNode } from './';

export const heuristic = (a: NNode, b: NNode) => {
    let d = 0;
    // d = distanceManhattan(a.x, a.y, a.z, b.x, b.y, b.z); // find the Manhattan distance (best for grids)
    d = distanceEuclidean(a.x, a.y, a.z, b.x, b.y, b.z); // euclidean - find the direct distance
    return d;
};

export const distanceManhattan = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
    return Math.abs(x0 - x1) + Math.abs(y0 - y1) + Math.abs(z0 - z1);
};

export const distanceEuclidean = (x0: number, y0: number, z0: number, x1: number, y1: number, z1: number) => {
    return Math.sqrt((x0 - x1) * (x0 - x1) + (y0 - y1) * (y0 - y1) + (z0 - z1) * (z0 - z1));
};

export const renderCell = (ctx: CanvasRenderingContext2D, node: NNode, col: string, cellSize: number) => {
    ctx.fillStyle = col;
    ctx.beginPath();
    ctx.rect(node.x * cellSize, node.y * cellSize, cellSize - 1, cellSize - 1);
    ctx.closePath();
    ctx.fill();
};